.profile {
  display: block;
  padding-top: 40px;
  font-size: 16px;
  width: 300px;
  border: 2px solid;
  border-color: rgb(156, 55, 195);
  background-color: aliceblue;
  justify-content: center;
  margin: 0 auto;
}
.avatar {
  border-radius: 50%;
  height: 100px;
  width: 100px;
}
.description {
  text-align: center;
  border-bottom: 2px solid;
  border-color: rgb(156, 55, 195);
}
.name {
  font-size: 24px;
  font-weight: 700;
}
.stats {
  list-style: none;
  text-align: center;
  justify-content: center;
  padding-left: 30px;
}
.stats_item {
  display: flex;
  flex-wrap: wrap;
}
.label {
  margin-right: 15px;

  margin-bottom: 10px;
}
.quantity {
  margin-right: 20px;
  justify-content: center;
  font-size: 18px;
  font-weight: 500;
}
