.title {
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
}
.statistics {
  border: 2px solid;
  border-top: none;
  border-color: rgb(156, 55, 195);
  background-color: aliceblue;
  margin-top: 0;
  padding-top: 30px;
  margin: 0 auto;
  width: 300px;
  display: block;
  text-align: center;
}
.stat_list {
  list-style: none;
  display: flex;
  font-size: 16px;
  padding-inline-start: 20px;
}
.item {
  text-align: center;

  display: flex;
  flex-wrap: wrap;
}
.label {
  padding-right: 15px;
  margin-bottom: 10px;
}
.percentage {
  padding-right: 15px;
  font-size: 20px;
}
